body{
  background-color: whtie;
  font-size: 1rem;
  font-family: monospace;
}
.profile-container {
  width: 350px;
  height: auto;
  background-color: lightgray;
  border-radius: 15px;
  padding: 30px;
  text-align: center;
  margin: 40px auto;
}
.profile {
  margin: 2px auto;
   width: 150px;
   height: 150px; 
}
.photo {
   width: 100%;
   height: 100%;
   border-radius: 50%;
}
.function{
  margin: 20px auto;
}
button{
  background-color:white;
  font-size: 20px; 
  padding: 4px 8px;
  width: 6em;
  margin: 4px;
  border: none;
}
.likes button{
  padding: 10px;
  background: none;
  border: none;
  outline: none;
}
.social{
  margin: 10px auto;

}